@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100&display=swap);
header {
    padding-top: 3rem;
    overflow: hidden;
}

.header__container {
    margin-top: -3rem;
    text-align: center;
    position: relative;
}

header h1 {
    font-weight: 100;
    font-size: 2.5rem;
    margin-top: 2.5rem;
}

.container.header__header {
    display: flex;
    max-width: 80%;
}

.container.head__container h5,
h4 {
    font-size: 2.5rem;
    -webkit-animation: letters-move 5s infinite ease-in-out;
            animation: letters-move 5s infinite ease-in-out;
    text-shadow: 3px 3px 0px var(--color-primary);
}

.container.head__container h1 {
    font-size: 2.5rem;
    -webkit-animation: letters-move 3s infinite ease-in-out;
            animation: letters-move 3s infinite ease-in-out;
}

.cta {
    display: inline-flex;
    align-items: baseline;
    margin-top: 2.5rem;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me {
    background: linear-gradient(var(--color-primary), transparent);
    display: none;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
    width: 25%;
    height: 60%;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
}

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;
}

/* RESPONSIVE */

/* MEDIA QUERIES (MEDIUM DEVICES) */

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }

}

/* MEDIA QUERIES (SMALL DEVICES) */

@media screen and (max-width:600px) {
    .container {
        width: var(--container-width-ms);
    }

    section>h2 {
        margin-bottom: 2rem;
    }

    .cta {
        flex-direction: column;
    }
}

@media screen and (max-width:400px) {
    header {
        margin-bottom: 5rem;
    }

    .cta {
        display: inline-block;
    }
}

/* ANIMATION */

@-webkit-keyframes letters-move {
    0% {
        -webkit-transform: translate(3px, -3px);
                transform: translate(3px, -3px);
    }

    25% {
        -webkit-transform: translate(0px, -3px);
                transform: translate(0px, -3px);
    }

    50% {
        -webkit-transform: translate(3px, 0px);
                transform: translate(3px, 0px);
    }

    75% {
        -webkit-transform: translate(0px, -3px);
                transform: translate(0px, -3px);
    }

    100% {
        -webkit-transform: translate(3px, -3px);
                transform: translate(3px, -3px);
    }
}

@keyframes letters-move {
    0% {
        -webkit-transform: translate(3px, -3px);
                transform: translate(3px, -3px);
    }

    25% {
        -webkit-transform: translate(0px, -3px);
                transform: translate(0px, -3px);
    }

    50% {
        -webkit-transform: translate(3px, 0px);
                transform: translate(3px, 0px);
    }

    75% {
        -webkit-transform: translate(0px, -3px);
                transform: translate(0px, -3px);
    }

    100% {
        -webkit-transform: translate(3px, -3px);
                transform: translate(3px, -3px);
    }
}
nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9em;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-primary);
    color: white;
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
    align-items: center;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

img.about__me {
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: 0.3s;
}


img.about__me:hover {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: white;
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: white;
}

/* MEDIA QUERIES (MEDIUEM DEVICES) */

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
 
    .about__me {
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0;
    }
}

/* MEDIA QUERIES (SMALL DEVICES) */

@media screen and (max-width:600px) {
    .about__me {
        width: 100%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }

    .experience__under1 {
        flex-direction: column;
    }

}


@media screen and (max-width:400px) {
    .about__container {
        margin-top: 3rem;
    }

    .about__cards {
        margin: 0 1rem 0 0;
    }

    .about__me {
        width: 90%;
        background: none;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    .img.about__me {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
    }
}




