footer {
    background: var(--color-primary);
    padding: 2rem;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

.footer__top {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 3rem;
}

footer a {
    color: var(--color-bg);
    border: 1px solid var(--color-bg);
    border-radius: 1rem;
    padding: 0.4rem;
}

a.footer__logo {
    border: none;
    font-size: 2.5rem;
    animation: letters-move 5s infinite ease-in-out;
    color: white;
    text-shadow: 3px 3px 0px var(--color-bg);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    width: 20%;
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a {
    background: var(--color-bg);
    color: white;
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

/* MEDIA QUERIES (MEDIUM DEVICES) */

@media screen and (max-width: 1024px) {

}

/* MEDIA QUERIES (SMALL DEVICES) */

@media screen and (max-width:600px) {
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
      }
    
      .footer__socials {
        margin-bottom: 2.6rem;
      }
}