@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100&display=swap');

:root {
    /* color: white;
    background: linear-gradient(90deg, rgba(1,0,54,1) 18%, rgba(17,18,66,1) 76%, rgb(58, 35, 142) 100%); */
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant:rgba(77,181,255,0.4);
    --color-secondary: #ff0080;
    --color-thirdary: #00b31b;
    --color-white:#fff;
    --color-light:rgba(255, 255, 255, 0.6);
    --container-width-lg: 75%;
    --container-width-md: 85%;
    --container-width-ms: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
}

* {
    margin: 0 auto;
    padding: 0 auto;
    border: 0;
    box-sizing: border-box;
    list-style: none;
    outline: 0;
    text-decoration: none;
}

/* GENERAL */

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: all 400ms ease;
}

a:hover {
    color:var(--color-white);
}

.btn {
width: max-content;
display: inline-block;
color:var(--color-primary);
padding: 0.75rem 1.2rem;
border-radius:0.4rem;
cursor: pointer;
border: 1px solid var(--color-primary);
transition: var(--transition);
}

.btn:hover {
    background:var(--color-white);
    color:var(--color-bg);
    border-color:transparent;
}
 
.btn-primary {
    background:var(--color-primary);
    color:var(--color-bg);
}

.btn-secondary {
    background: transparent;
    color: white;
    border-color: white;
    display: flex;
    align-items: baseline;
    justify-content: center;
}

.btn-secondary-image {
    margin-left: 6px;
}

.btn.btn-secondary:hover {
    transition: 0.1s;
    color:var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* MEDIA QUERIES (BIG DEVICES) */

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

/* MEDIA QUERIES (SMALL DEVICES) */

    @media screen and (max-width:600px) {
        header {
            height: 100vh;
        }

        .header__socials, .scroll__down {
            display: none;
        }
}